.basic-footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  z-index: 1001;
  text-align: center;
  background: rgba(255, 255, 255, 0.8);
  border-top: 0.013333rem solid rgba(222, 222, 222, 0.5);
}

.basic-footer :global .am-tab-bar-tab {
  width: 20%;
  display: inline-block;
  padding: 0.133333rem 0 0.053333rem 0;
}

.basic-footer :global .am-tab-bar-tab p {
  font-size: 0.266667rem;
  line-height: 1.5;
  color: #03AF98;
  padding: 0.08rem 0 0 0;
}

