.login {
  background: #fff;
}

.login .logo {
  margin-top: 2.266667rem;
  margin-bottom: 2.333333rem;
}

.login .login-name {
  text-align: center;
}

.login .login-name img {
  width: 2.56rem;
}

.login .login-form {
  padding: 0.213333rem 0.96rem 0 0.96rem;
}

.login .login-form .form-label {
  display: block;
  font-size: 0.48rem;
  font-family: PingFang-SC-Medium;
  font-weight: 500;
  color: #303030;
  line-height: 0.48rem;
  padding: 0.4rem 0 0.24rem 0;
}

.login .login-form .form-title {
  background-size: 100% auto;
  display: inline-block;
  color: #34332E;
  font-size: 0.693333rem;
  line-height: 0.746667rem;
  font-weight: normal;
  padding: 0.04rem 0.066667rem;
  margin-bottom: 0.8rem;
}

.login .login-form .form-row {
  -ms-flex-align: center;
      align-items: center;
  margin-top: 0.266667rem;
  color: #34ADFE;
  border-bottom: 0.026667rem solid #F1F1F1;
}

.login .login-form .form-row .input {
  -ms-flex: 1 1;
      flex: 1 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  position: relative;
}

.login .login-form .form-row .input input {
  -ms-flex: 1 1;
      flex: 1 1;
  width: 100%;
  padding: 0.32rem 0;
  background: none;
  outline: none;
  font-size: 0.426667rem;
  border: 0;
  caret-color: #03AF98;
  color: #303030;
}

.login .layerBox {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.4);
}

.login .layerBox .layer-inner {
  background: #fff;
  border-radius: 0.106667rem;
  width: 7.333333rem;
  height: 4.533333rem;
  position: absolute;
  left: 50%;
  top: 45%;
  margin-left: -3.666667rem;
  margin-top: -2.266667rem;
  padding: 0.426667rem;
  overflow: hidden;
}

.login .layerBox .layer-inner .layer-title {
  text-align: center;
  font-size: 0.426667rem;
  font-family: PingFang-SC-Medium;
  font-weight: 500;
  color: #303030;
  line-height: 0.426667rem;
}

.login .layerBox .layer-inner .layer-code {
  position: relative;
  background: #f3f3f3;
  width: 100%;
  height: 1.28rem;
  margin-top: 0.64rem;
}

.login .layerBox .layer-inner .layer-code .input {
  height: 100%;
  padding-left: 0.266667rem;
  -ms-flex: 1 1;
      flex: 1 1;
  background: none;
  outline: none;
  font-size: 0.426667rem;
  border: 0;
  caret-color: #03AF98;
}

.login .layerBox .layer-inner .layer-btn {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1.12rem;
  line-height: 1.12rem;
  border-top: 0.026667rem solid #F1F1F1;
}

.login .layerBox .layer-inner .layer-btn a {
  width: 50%;
  text-align: center;
  display: inline-block;
  color: #303030;
  font-size: 0.426667rem;
}

.login .layerBox .layer-inner .layer-btn .layer-cancle {
  border-right: 0.026667rem solid #F1F1F1;
  color: #ACACAC;
}

:global .display_none {
  display: none !important;
}

:global .display_block {
  display: block !important;
}

:global .icon-logo {
  display: block;
  margin: 0 auto;
  width: 1.706667rem;
  margin-top: 1.653333rem;
  margin-bottom: 0.533333rem;
}

:global .login-go-back {
  left: 0.24rem;
  top: 0.293333rem;
  position: fixed;
}

:global .login-go-back img {
  width: 0.666667rem;
  height: 0.666667rem;
  padding: 0.133333rem;
}

:global .captchaCode {
  width: 2.666667rem;
  display: inline-block;
}

:global .login-getCode {
  color: #FFBE0A;
  font-size: 0.373333rem;
  width: 2.666667rem;
  text-align: right;
}

:global .login-getCode:hover,
:global .login-getCode:active {
  color: #FFBE0A;
}

:global ::-webkit-input-placeholder {
  /* WebKit, Blink, Edge */
  color: #E2DADA;
  font-size: 0.426667rem;
}

:global :-moz-placeholder {
  /* Mozilla Firefox 4 to 18 */
  color: #E2DADA;
  font-size: 0.426667rem;
}

:global ::-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: #E2DADA;
  font-size: 0.426667rem;
}

:global :-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: #E2DADA;
  font-size: 0.426667rem;
}

:global .submit-button {
  margin-top: 1.066667rem;
  height: 1.226667rem;
  background: -webkit-gradient(linear, left top, right top, from(rgba(5, 170, 186, 0.2)), to(rgba(0, 210, 152, 0.2)));
  background: -webkit-linear-gradient(left, rgba(5, 170, 186, 0.2) 0%, rgba(0, 210, 152, 0.2) 100%);
  background: -o-linear-gradient(left, rgba(5, 170, 186, 0.2) 0%, rgba(0, 210, 152, 0.2) 100%);
  background: linear-gradient(90deg, rgba(5, 170, 186, 0.2) 0%, rgba(0, 210, 152, 0.2) 100%);
  border-radius: 0.106667rem;
  text-align: center;
  line-height: 1.226667rem;
  color: #fff;
  font-size: 0.426667rem;
}

:global .submit-button :global .am-button-primary {
  border-radius: 1.333333rem;
  display: block;
}

:global .login-sure-send {
  background: -webkit-gradient(linear, left top, right top, from(#05aaba), to(#00d298));
  background: -webkit-linear-gradient(left, #05aaba 0%, #00d298 100%);
  background: -o-linear-gradient(left, #05aaba 0%, #00d298 100%);
  background: linear-gradient(90deg, #05aaba 0%, #00d298 100%);
}

:global .code-button {
  display: inline-block;
  width: 2.133333rem;
  height: 0.8rem;
  line-height: 0.8rem;
  font-weight: 500;
  color: rgba(3, 175, 152, 0.2) !important;
  text-align: center;
  font-size: 0.373333rem;
}

:global .sure-send {
  color: #03af98 !important;
