.basic-header {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 1002;
  width: 100%;
  color: #000;
  background: #ffffff;
  -webkit-box-shadow: 0 0.053333rem 0.08rem 0 rgba(139, 2, 34, 0.04);
          box-shadow: 0 0.053333rem 0.08rem 0 rgba(139, 2, 34, 0.04);
  height: 1.173333rem;
  color: #34332E;
  font-size: 0.48rem;
  text-align: center;
  line-height: 1.173333rem;
}

.basic-header img {
  position: absolute;
  left: 0.213333rem;
  top: 0.24rem;
  width: 0.64rem;
  padding: 0.08rem;
}

.basic-header :global .am-icon-md {
  width: 0.933333rem;
  height: 1.173333rem;
}

.basic-header :global .am-navbar-left,
.basic-header :global .am-navbar-right {
  display: none;
  height: 0% !important;
}

.basic-header :global .am-navbar-title {
  width: 100%;
  text-align: center;
  height: 1.173333rem;
  line-height: 1.173333rem;
  position: absolute;
  top: 0;
  font-size: 0.453333rem;
  z-index: -1;
  color: #34332E;
}

.basic-header :global .am-navbar-title b {
  color: #514100;
  position: absolute;
  left: 1.333333rem;
  font-weight: normal;
  font-size: 0.426667rem;
}

:global img.ttbf-title-logo {
  position: initial;
  width: 2.133333rem;
  height: 0.533333rem;
  display: inline-block;
  margin: 0.32rem auto;
}

:global .header-logo {
  width: 2.56rem !important;
  position: inherit !important;
  display: block;
  margin: 0 auto;
  left: auto !important;
}


