  position: relative;
  width: 100%;
  height: 100%;
}

:global #root .switch-wrapper > div {
  position: absolute;
  width: 100%;
  height: 100%;
}

:global #root .am-pull-to-refresh-down .am-pull-to-refresh-indicator {
  font-size: 0.32rem;
}

